<template>
    <div class="settings overflow-auto">
        <div class="form-group">
            <label for="editGroupName" class="label">Group name</label>
            <div class="input-group">
                <input type="text" id="editGroupName" :value="groupName" v-on:input="updateGroupName" v-bind:class="{ 'is-invalid': groupNameIsInvalid}" class="form-control" placeholder="Add a new group name">
                <div v-if="groups.length > 1" class="input-group-append">
                    <button class="btn btn-danger" type="button" @click="deleteGroup()">Delete</button>
                </div>
            </div>
        </div>

        <div class="form-group">
            <label for="editMockupName" class="label">Mockup name:</label>
            <div class="input-group">
                <input type="text" id="editMockupName" :value="mockupName" v-on:input="updateMockupName" v-bind:class="{ 'is-invalid': mockupNameIsInvalid}" class="form-control" placeholder="Add a new mockup name">
                 <div v-if="mockups.length > 1" class="input-group-append">
                    <button class="btn btn-danger" type="button" @click="deleteMockup()">Delete</button>
                </div>
            </div>
        </div>
        <div class="form-group">
            <label for="editMockupPosition" class="label">Mockup position</label>
            <input type="number" id="editMockupPosition" :value="mockupPosition" v-on:input="updateMockupPosition" class="form-control" placeholder="Add a new mockup position">
        </div>
        <div class="form-group" v-if="widgets.length > 0">
            <label class="label">Widgets</label>
             <div v-for="(widget, index) in widgets" :key="index" style="margin-top:10px">
                <div class="input-group mb-3">
                  <div class="input-group-prepend">
                    <div class="input-group-text">
                        <input type="checkbox" v-model="widget.active" @change="switchActiveStatus($event, widget.id)" aria-label="Checkbox for following text input">
                    </div>
                    <span class="input-group-text">{{ widget.type}}</span>
                  </div>
                    <input type="text" class="form-control" placeholder="WidgetName" :value="widget.name || widget.id" readonly>
                    <div class="input-group-append">
                        <button class="btn btn-outline-secondary" :id="'settings-widget-' + widget.id" type="button" data-toggle="modal" data-target="#editMockupModal" data-type="mockup" @click="setCurrentWidget(widget.id)">Details</button>
                    </div>
                </div>
            </div>
        </div>
        <div v-else>
                Widgets missing at current mockup
        </div>
        <div class="modal fade" id="editMockupModal"  ref="myModalRef" tabindex="-1" role="dialog" aria-labelledby="editMockupModalLabel" aria-hidden="true">
          <div class="modal-dialog" role="document">
            <div class="modal-content">
              <div class="modal-header">
                <h5 class="modal-title" id="editMockupModalLabel">Edit</h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div class="modal-body">
                  <div class="form-group">
                      <label for="editWidgetName" style="padding-left: 10px">Widget name</label>
                      <input type="text" id="editWidgetName" :value="currentWidget.name || currentWidget.id" v-on:input="updateWidgetName" class="form-control" placeholder="Add a new widget name">
                  </div>
                  <div class="form-group" v-if="possibleMockups.length > 0">
                      <label for="addWidgetToMockup" style="padding-left: 10px">Add to existing mockup</label>
                      <div class="input-group">
                          <select class="custom-select" id="addWidgetToMockup">
                            <option value="0" selected>Choose...</option>
                            <option value="-1" selected>Add to all mockups.</option>

                            <option v-for="(mockup, mockupIndex) in possibleMockups" v-bind:key="mockupIndex" v-bind:value="mockup.id">
                                {{ mockup.name }}
                            </option>
                          </select>
                          <div class="input-group-append">
                            <button class="btn btn-success" type="button" @click="addWidgetToMockup">add</button>
                          </div>
                      </div>
                  </div>
                  <div class="form-group" v-if="currentMockups.length > 1">
                      <label for="removeWidgetFromMockup" style="padding-left: 10px">Remove from mockup</label>
                      <div class="input-group">
                          <select class="custom-select" id="removeWidgetFromMockup">
                            <option value="0" selected>Choose...</option>
                            <option v-for="(mockup, mockupIndex) in currentMockups" v-bind:key="mockupIndex" v-bind:value="mockup.id">
                                {{ mockup.name }}
                            </option>
                          </select>
                          <div class="input-group-append">
                            <button class="btn btn-warning" type="button" @click="removeWidgetFromMockup">remove</button>
                          </div>
                      </div>
                  </div>
                  <button v-if="currentMockups.length > 1" class="btn btn-info" style="margin-bottom: 5px" type="button" @click="cloneWidget">Clone</button>
                  <button class="btn btn-danger" type="button" @click="deleteWidget()">Delete completly</button>

                  <div v-if="currentWidgetSettings">
                      <hr>
                      <div class="form-group" v-for="(setting, index) in currentWidgetSettings" :key="index">
                        <label :for="index" style="padding-left: 10px">{{index}}</label>
                          <div v-if="index.indexOf('image') !== -1">
                              <input type="file" @change="updateImage($event.target.name, $event.target.files)" accept="image/*">

                          </div>
                        <input v-else-if="index.indexOf('style') === -1" type="text" class="form-control" :value="setting" :data-name="index" v-on:input="updateWidgetSettings">
                        <input v-else-if="index.indexOf('style-font') !== -1" type="number" class="form-control" :value="setting" :data-name="index" v-on:input="updateWidgetSettings">
                          <div class="form__field" v-else>
                            <div class="form__input" >
                                <swatches
                                    v-model="currentWidget.settings[index]" v-on:input="updateSetting($event, index)"
                                    show-fallback
                                    popover-to="left"
                                    colors="material-dark"
                                    row-length="6">
                                </swatches>
                            </div>
                          </div>
                      </div>
                  </div>
              </div>
              <div class="modal-footer">
                <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
              </div>
            </div>
          </div>
        </div>
    </div>
</template>

<script>
// TODO remove jquery ?
import $ from 'jquery'
import swatches from 'vue-swatches'
import 'vue-swatches/dist/vue-swatches.min.css'

export default {
  name: 'Settings',
  data () {
    return {
      mockupNameIsInvalid: false,
      groupNameIsInvalid: false,
      latestImageId: ''
    }
  },
  components: {swatches},
  methods: {
    setCurrentWidget (widgetId) {
      this.$store.dispatch('SET_CURRENT_WIDGET', widgetId)
    },
    addWidgetToMockup () {
      let mockupId = $('#addWidgetToMockup').val()
      if (mockupId !== '0') {
        this.$store.dispatch('ADD_CURRENT_WIDGET_TO_MOCKUP', mockupId)
      }
    },
    removeWidgetFromMockup () {
      let mockupId = $('#removeWidgetFromMockup').val()
      if (mockupId !== '0') {
        this.$store.dispatch('REMOVE_CURRENT_WIDGET_FROM_MOCKUP', mockupId)
      }
    },
    updateImage: function (e, s) {
      let file = document.querySelector('input[type=file]').files[0]
      let reader = new FileReader()
      let store = this.$store
      // reader.addEventListener('load', function () {
      //   let image = document.createElementNS(null, 'image')
      //   image.className = 'resizeable'
      //   image.setAttribute('data-setting-image', 1)
      //   image.setAttributeNS('http://www.w3.org/1999/xlink', 'href', reader.result)
      //   alert(image.width)
      //   let payload = {
      //     'settings': {
      //       'image': reader.result
      //     },
      //     'svgData': image.outerHTML,
      //     'resetPosition': true
      //   }
      //   store.dispatch('UPDATE_WIDGET_SETTINGS', payload)
      // }, false)

      reader.onload = function () {
        var image2 = new Image()
        image2.src = reader.result
        let width = 0
        let height = 0
        image2.onload = function () {
          let widget = store.getters.getCurrentWidget
          let element = document.getElementById(widget.id).firstElementChild
          element.setAttribute('height', image2.height)
          element.setAttribute('width', image2.width)
        }
        let image = document.createElementNS(null, 'image')
        image.className = 'resizeable'
        image.setAttribute('height', height)
        image.setAttribute('width', width)
        image.setAttribute('data-setting-image', 1)
        image.setAttributeNS('http://www.w3.org/1999/xlink', 'href', reader.result)
        let payload = {
          'settings': {
            'image': reader.result
          },
          'svgData': image.outerHTML,
          'resetPosition': true
        }
        store.dispatch('UPDATE_WIDGET_SETTINGS', payload)
      }

      if (file) {
        reader.readAsDataURL(file)
      }
    },
    updateSetting: function (event, setting) {
      let widget = this.$store.getters.getCurrentWidget
      let div = document.createElement('div')
      div.innerHTML = widget.svgData.trim()
      let startIndex = setting.indexOf('style') + 6
      div.querySelectorAll('[data-setting-' + setting + '="1"]').item(0).style[setting.substr(startIndex)] = event
      let payload = {
        'setting': setting,
        'color': event,
        'svgData': div.innerHTML
      }
      this.$store.dispatch('UPDATE_WIDGET_SETTING', payload)
    },
    updateWidgetName: function (event) {
      let data = {
        'name': event.target.value
      }
      this.$store.dispatch('UPDATE_WIDGET', data)
    },
    updateGroupName: function (event) {
      if (event.target.value.length === 0) {
        this.groupNameIsInvalid = true
      } else {
        let data = {
          'name': event.target.value
        }
        this.$store.dispatch('UPDATE_GROUP', data)
        this.groupNameIsInvalid = false
      }
    },
    updateMockupName: function (event) {
      if (event.target.value.length === 0) {
        this.mockupNameIsInvalid = true
      } else {
        let data = {
          'name': event.target.value
        }
        this.$store.dispatch('UPDATE_MOCKUP', data)
        this.mockupNameIsInvalid = false
      }
    },
    updateMockupPosition: function (event) {
      let data = {
        'position': event.target.value
      }
      this.$store.dispatch('UPDATE_MOCKUP', data)
    },
    switchActiveStatus: function (event, widgetId) {
      this.$store.dispatch('SET_CURRENT_WIDGET', widgetId)

      let data = {
        'active': event.currentTarget.checked
      }
      this.$store.dispatch('UPDATE_WIDGET', data)
    },
    deleteWidget: function () {
      this.$store.dispatch('DELETE_WIDGET')
      $('[data-dismiss=modal]').trigger({type: 'click'})
    },
    deleteMockup: function () {
      this.$store.dispatch('DELETE_MOCKUP')
    },
    deleteGroup: function () {
      this.$store.dispatch('DELETE_GROUP')
    },
    cloneWidget: function () {
      this.$store.dispatch('CLONE_WIDGET')
    },
    updateWidgetSettings: function (event) {
      let widget = this.$store.getters.getCurrentWidget
      let div = document.createElement('div')
      div.innerHTML = widget.svgData.trim()
      let startIndex = event.target.dataset.name.indexOf('style') + 6

      if (event.target.dataset.name.indexOf('style') === -1) {
        div.querySelectorAll('[data-setting-' + event.target.dataset.name + '="1"]').item(0).innerHTML = event.target.value
      } else if (event.target.dataset.name.indexOf('style-font') !== -1) {
        div.querySelectorAll('[data-setting-' + event.target.dataset.name + '="1"]').item(0).style[event.target.dataset.name.substr(startIndex)] = event.target.value + 'px'
      } else {
        div.querySelectorAll('[data-setting-' + event.target.dataset.name + '="1"]').item(0).style[event.target.dataset.name.substr(startIndex)] = event.target.value
      }

      let payload = {
        'svgData': div.innerHTML,
        'settings': {
          [event.target.dataset.name]: event.target.value
        }
      }

      this.$store.dispatch('UPDATE_WIDGET_SETTINGS', payload)
    }
  },
  computed: {
    mockupName () {
      let currentMockup = this.$store.getters.getCurrentMockup
      if (currentMockup !== null) {
        return currentMockup.name
      }
      return ''
    },
    groupName () {
      let currentMockup = this.$store.getters.getCurrentMockup
      if (currentMockup !== null) {
        let group = this.$store.getters.getGroupById(currentMockup.groupId)
        return group.name
      }
      return ''
    },
    mockupPosition () {
      let currentMockup = this.$store.getters.getCurrentMockup
      if (currentMockup !== null) {
        return currentMockup.position
      }
      return 0
    },
    widgets () {
      let widgets = this.$store.getters.getCurrentWidgets
      return widgets
    },
    currentWidgetSettings () {
      let widget = this.$store.getters.getCurrentWidget
      if (widget !== null) {
        return widget.settings
      }
      return []
    },
    currentWidget () {
      let widget = this.$store.getters.getCurrentWidget
      if (widget === null) {
        return {
          'name': '',
          'id': ''
        }
      }
      return widget
    },
    possibleMockups () {
      let mockups = this.$store.getters.getMockups
      let currentWidget = this.$store.getters.getCurrentWidget
      mockups = mockups.filter(function (mockup) {
        let existing = false
        mockup.widgetIds.forEach(function (widgetId) {
          if (widgetId === currentWidget.id) {
            existing = true
          }
        })
        return !existing
      })
      return mockups
    },
    currentMockups () {
      let mockups = this.$store.getters.getMockups
      let currentWidget = this.$store.getters.getCurrentWidget
      mockups = mockups.filter(function (mockup) {
        let existing = false
        mockup.widgetIds.forEach(function (widgetId) {
          if (widgetId === currentWidget.id) {
            existing = true
          }
        })
        return existing
      })
      return mockups
    },
    mockups () {
      return this.$store.getters.getMockups
    },
    groups () {
      return this.$store.getters.getGroups
    }
  }
}
</script>

<style>
    .label {
        padding-left: 10px
    }
</style>



