<template>
    <div v-bind:id="'mockup-' + id" class="mockup" v-bind:class="{ active: isActive }" @click="switchMockup()">
            {{ name }}
    </div>
</template>

<script>
export default {
  name: 'Mockup',
  props: {
    id: String,
    name: String,
    active: {
      type: Boolean,
      default: false
    }
  },
  components: {},
  methods: {
    switchMockup () {
      this.$store.dispatch('SET_CURRENT_MOCKUP', this.id)
    }
  },
  computed: {
    isActive () {
      return this.$store.getters.getMockupsByIds(this.id)[0].active
    }
  }
}
</script>

<style scoped>
    .mockup {
        padding: 10px;
        overflow: auto;
        clear:both;
        background-color: rgba(0,0,0,.04);
    }
    .mockup:hover {
        background-color: rgba(0,0,0,.08);
        cursor: pointer;
    }
    .active {
        background-color: rgba(0,0,0,.08);
    }
</style>



