<template>
    <div title="Image Widget" id="imageWidget" class="widgetBar-element" @click="addNewWidget()">
        <svg id="image-widget-preview" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32" style="width: 24px;height: 24px;">
          <title>image_preview</title>
          <g>
            <rect x="0.25" y="0.25" width="31.5" height="31.5" style="fill: #fff"/>
            <path d="M31.5.5v31H.5V.5h31M32,0H0V32H32V0Z"/>
          </g>
          <polygon points="3.5 23.5 9.5 14.5 13.5 18.5 20.5 9.5 28.5 23.5 3.5 23.5" style="stroke: #000;stroke-miterlimit: 10;stroke-width: 0.5px"/>
        </svg>
        <span>Image</span>
    </div>
</template>

<script>

import uuid from 'uuid'

export default {
  name: 'image-widget',
  data () {
    return {
      svgData: '<image width="185" height="40" class="resizeable" data-setting-image="1" xlink:href="data:image/svg+xml;base64,"/>'
    }
  },
  methods: {
    addNewWidget () {
      let mockup = this.$store.getters.getCurrentMockup
      let widget = {
        'id': uuid.v4(),
        'type': 'Image',
        'active': true,
        'name': '',
        'svgData': this.svgData,
        'mockupIds': [
          mockup.id
        ],
        'x': 0,
        'y': 0,
        'settings': {
          'image': '/static/logo-main.svg'
        }
      }
      this.$store.dispatch('ADD_WIDGET', widget)
    }
  }
}
</script>

<style>
    .button-widget-border {
        fill: #ccc;
    }
    .button-widget-text {
        font-size: 20px;
        font-family: ArialMT, Arial;
    }
</style>



