<template>
    <div class="col-md-2 widgetBar">
        <div class="widgetBar-menu">
            <InputWidget/>
            <ButtonWidget/>
            <TextWidget/>
            <DivWidget/>
            <ImageWidget/>
        </div>
    </div>
</template>

<script>

import InputWidget from '@/components/widgets/InputWidget'
import ButtonWidget from '@/components/widgets/ButtonWidget'
import TextWidget from '@/components/widgets/TextWidget'
import DivWidget from '@/components/widgets/DivWidget'
import ImageWidget from '@/components/widgets/ImageWidget'

export default {
  name: 'WidgetBar',
  components: {
    DivWidget,
    TextWidget,
    ButtonWidget,
    InputWidget,
    ImageWidget
  }
}
</script>

<style>
    .widgetBar {
        box-shadow: inset -2px 0 0 rgba(0, 0, 0, .3);
        padding-right: 0px;
        padding-left: 0px;
        background-color: rgba(0, 0, 0, .1);

    }
    .widgetBar-menu {
        width: 100%;
        height: 100%;
        display: grid;
        margin: 1px;
        grid-template-columns: repeat(3,1fr);
        grid-auto-rows: minmax(1px,72px);
    }
    .widgetBar-element {
        cursor: pointer;
        padding-top: 1em;
        text-align: center;
        background-color: transparent;
    }
    .widgetBar-element:hover {
        background-color: rgba(0,0,0,.08);
    }
    .widgetBar-element span {
        display: block;
        overflow: hidden;
        text-overflow: ellipsis;
        font-size: small;
        padding: 0 8px;
    }
</style>



