<template>
    <div class="exports">
        <button type="button" class="btn btn-outline-secondary" data-toggle="modal" data-target="#newProjectExport" data-type="group">Export project</button>
        <button type="button" class="btn btn-outline-secondary" @click="openFileUpload()">Import project</button>
        <input type="file" id="fileUpload" multiple accept="application/json" style="display:none" @change="importProjectFromJSON($event)">

        <button type="button" class="btn btn-outline-secondary" data-toggle="modal" data-target="#newSVG" data-type="group">Generate SVG</button>
        <button type="button" class="btn btn-outline-secondary" data-toggle="modal" data-target="#newPresentationExport" data-type="group">Generate Presentation File</button>

        <div class="modal fade bd-example-modal-lg" id="newProjectExport" tabindex="-1" role="dialog" aria-labelledby="projectModalLabel" aria-hidden="true">
          <div class="modal-dialog" role="document">
            <div class="modal-content">
              <div class="modal-header">
                <h5 class="modal-title" id="projectModalLabel">Generate Project Json</h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div class="modal-body">
                   <div class="form-group">
                      <label for="projectFilename" style="padding-left: 10px">Filename</label>
                      <input type="text" id="projectFilename" :value="projectFilename" v-on:input="updateProjectName" class="form-control" placeholder="Add a filename">
                  </div>
              </div>
              <div class="modal-footer">
                <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                <button type="button" class="btn btn-success" @click="generateProjectJSON()">Generate</button>
              </div>
            </div>
          </div>
        </div>
         <div class="modal fade bd-example-modal-lg" id="newSVG" tabindex="-1" role="dialog" aria-labelledby="SVGModalLabel" aria-hidden="true">
          <div class="modal-dialog" role="document">
            <div class="modal-content">
              <div class="modal-header">
                <h5 class="modal-title" id="SVGModalLabel">Generate SVG</h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div class="modal-body">
                   <div class="form-group">
                      <label for="svgFilename" style="padding-left: 10px">Filename</label>
                      <input type="text" id="svgFilename" :value="svgFilename" v-on:input="updateSVGName" class="form-control" placeholder="Add a filename">
                  </div>
              </div>
              <div class="modal-footer">
                <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                <button type="button" class="btn btn-success" @click="generateSVG()">Generate</button>
              </div>
            </div>
          </div>
        </div>
        <div class="modal fade bd-example-modal-lg" id="newPresentationExport" tabindex="-1" role="dialog" aria-labelledby="presenationModalLabel" aria-hidden="true">
          <div class="modal-dialog" role="document">
            <div class="modal-content">
              <div class="modal-header">
                <h5 class="modal-title" id="presentationModalLabel">Generate Presenation File</h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div class="modal-body">
                  <div class="form-group">
                      <label for="chooseGroup" style="padding-left: 10px">Choose a Format</label>
                      <div class="input-group">
                          <select class="custom-select" id="chooseFormat">
                            <option value="md" selected>Markdown</option>
                            <option value="html">HTML</option>
                          </select>
                      </div>
                  </div>
                   <div class="form-group">
                      <label for="presentationFilename" style="padding-left: 10px">Filename</label>
                      <input type="text" id="presentationFilename" :value="presentationFilename" v-on:input="updatePresentationName" class="form-control" placeholder="Add a filename">
                  </div>
                  <div class="form-group">
                      <label for="chooseGroup" style="padding-left: 10px">Choose a group</label>
                      <div class="input-group">
                          <select class="custom-select" id="chooseGroup" @change="loadPossibleMockups()">
                            <option value="0" selected>Choose...</option>
                            <option v-for="(group, groupIndex) in groups" v-bind:key="groupIndex" v-bind:value="group.id">
                                {{ group.name }}
                            </option>
                          </select>
                      </div>
                  </div>
                  <div class="form-group" v-if="mockups.length > 0">
                      <label for="addMockupToPresentation" style="padding-left: 10px">Add mockup to Presentation</label>
                      <div class="input-group">
                          <select class="custom-select" id="addMockupToPresentation">
                            <option value="0" selected>Add all Mockups from group</option>
                            <option v-for="(mockup, mockupIndex) in mockups" v-bind:key="mockupIndex" v-bind:value="mockup.id">
                                {{ mockup.name }}
                            </option>
                          </select>
                          <div class="input-group-append">
                            <button class="btn btn-warning" type="button" @click="addMockupToPresentation()">add</button>
                          </div>
                      </div>
                  </div>
                  <table class="table table-sm" v-if="getSelectedMockups.length > 0">
                      <thead>
                        <tr>
                          <th scope="col">X</th>
                          <th>&uarr;</th>
                          <th>&darr;</th>
                          <th scope="col">Name</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr v-for="(mockup, mockupIndex) in getSelectedMockups" v-bind:key="mockupIndex">
                          <td>
                              <a href="#" @click="removeMockupFromMarkdown(mockup.id)">X</a>
                          </td>
                          <td>
                              <a href="#"  v-if="mockupIndex > 0" @click="sortSelectedMockups(mockup.id, 'up')">&uarr;</a>
                          </td>
                          <td>
                              <a href="#" v-if="mockupIndex < selectedMockups.length -1" @click="sortSelectedMockups(mockup.id, 'down')">&darr;</a>
                          </td>
                          <td>{{ mockup.name }}</td>
                        </tr>
                      </tbody>
                    </table>
              </div>
              <div class="modal-footer">
                <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                <button type="button" class="btn btn-success" @click="generatePresentation()">Generate</button>
              </div>
            </div>
          </div>
        </div>
    </div>
</template>

<script>

import {mapActions} from 'vuex'
import $ from 'jquery'
import JSZip from 'JSZip'
import { saveAs } from 'file-saver'

export default {
  name: 'Exports',
  data () {
    return {
      mockupNameIsInvalid: false,
      groupNameIsInvalid: false,
      projectFilename: 'scenario.json',
      svgFilename: 'scenario.svg',
      presentationFilename: 'scenario',
      mockups: [],
      selectedMockups: []
    }
  },
  components: {},
  methods: {
    ...mapActions([
      'importProjectFromJSON'
    ]),
    addMockupToPresentation () {
      let name = $('#addMockupToPresentation option:selected').text()
      let mockupId = $('#addMockupToPresentation').val()
      if (parseInt(mockupId) === 0) {
        let groupMockups = this.loadPossibleMockups()
        let selectedMockups = this.selectedMockups
        groupMockups.forEach(function (mockup, index, object) {
          selectedMockups.push({
            'name': mockup.name,
            'id': mockup.id
          })
        }, selectedMockups)
      } else {
        this.selectedMockups.push({
          'name': name,
          'id': mockupId
        })
      }
    },
    generatePresentation () {
      let store = this.$store
      let fileName = this.presentationFilename
      let md = '#Überschrift\n\n'
      let zip = new JSZip()
      let rootDir = zip.folder(fileName)
      let imgArray = []
      this.selectedMockups.forEach(function (tempMockup, index) {
        let mockup = store.getters.getMockupsByIds(tempMockup.id)[0]
        let text = '<svg version="1.1" viewBox="0 0 800 800" width="100%" height="100%" xmlns="http://www.w3.org/2000/svg"  xmlns:xlink="http://www.w3.org/1999/xlink">\n'
        mockup.widgetIds.forEach(function (widgetId) {
          let widget = store.getters.getWidgetsByIds(widgetId)[0]
          text = text + '<g id="' + widget.id + '" data-x="' + widget.x + '" data-y="' + widget.y + '" style="transform: translate(' + widget.x + 'px,' + widget.y + 'px)">'
          text = text + widget.svgData
          text = text + '</g>'
        })
        text = text + '</svg>'
        let tempFilename = fileName + '_' + index + '.svg'
        imgArray.push(tempFilename)
        md = md + '##Beschreibung\n'
        md = md + '![Alt text](images/' + tempFilename + ')\n\n\n'

        let images = rootDir.folder('images')
        images.file(tempFilename, text)
      })

      let format = $('#chooseFormat').val()
      if (format === 'html') {
        let dataFields = 'var imgArray = ['
        imgArray.forEach(function (tempImageFileName) {
          dataFields = dataFields + '"' + tempImageFileName + '",'
        })
        dataFields = dataFields.substr(0, dataFields.length - 1)
        dataFields = dataFields + '];'
        rootDir.file('data.js', dataFields)

        let content = document.getElementById('hiddenHTML').innerHTML

        rootDir.file('index.html', content)
      } else {
        // MARKDOWN
        rootDir.file(fileName + '.md', md)
      }
      zip.generateAsync({
        type: 'blob'
      }).then(function (content) {
        saveAs(content, fileName + '.zip')
      })
    },
    generateProjectJSON () {
      this.$store.dispatch('GENERATE_PROJECT_JSON', this.projectFilename)
    },
    generateSVG () {
      let text = document.getElementById('dropzone').outerHTML
      let blob = new Blob([text], { type: 'application/svg+xml' })
      let anchor = document.createElement('a')

      if (this.svgFilename.length === 0) {
        this.svgFilename = 'scenario.svg'
      } else if (this.svgFilename.endsWith('.svg') === false) {
        this.svgFilename = this.svgFilename + '.svg'
      }
      anchor.download = this.svgFilename
      anchor.href = (window.URL).createObjectURL(blob)
      anchor.dataset.downloadurl = ['application/svg+xml', anchor.download, anchor.href].join(':')
      anchor.click()
    },
    loadPossibleMockups () {
      let groupId = $('#chooseGroup').val()
      if (groupId === 0) {
        return []
      }
      this.mockups = this.$store.getters.getMockups.filter(mockup => mockup.groupId === groupId)
      return this.mockups
    },
    openFileUpload () {
      let fileUploadElement = document.getElementById('fileUpload')
      fileUploadElement.click()
    },
    updateProjectName: function (event) {
      this.projectFilename = event.target.value
    },
    updateSVGName: function (event) {
      this.svgFilename = event.target.value
    },
    updatePresentationName: function (event) {
      this.presentationFilename = event.target.value
    },
    removeMockupFromMarkdown (mockupId) {
      this.selectedMockups.forEach(function (mockup, index, object) {
        if (mockup.id === mockupId) {
          object.splice(index, 1)
        }
      })
    },
    sortSelectedMockups (mockupId, direction) {
      let index = this.selectedMockups.findIndex(mockup => mockup.id === mockupId)
      let temp = this.selectedMockups[index]
      if (direction === 'down') {
        this.selectedMockups[index] = this.selectedMockups[index + 1]
        this.selectedMockups[index + 1] = temp
      } else {
        this.selectedMockups[index] = this.selectedMockups[index - 1]
        this.selectedMockups[index - 1] = temp
      }
      this.$forceUpdate()
    }
  },
  computed: {
    groups () {
      return this.$store.getters.getGroups
    },
    getSelectedMockups () {
      return this.selectedMockups
    }
  },
  mounted: function () {
  }
}
</script>

<style>
    .label {
        padding-left: 10px
    }
</style>



