<template>
    <div class="mockupBar">
        <button type="button" class="btn btn-outline-secondary" data-toggle="modal" data-target="#newGroupModal" data-type="group">Create a new group</button>
        <button type="button" class="btn btn-outline-secondary" data-toggle="modal" data-target="#newMockupModal" data-type="mockup">Create a new mockup</button>

        <div class="accordion" id="groupAccordion">
            <div v-for="(group, groupIndex) in groups" :key="groupIndex" class="card">
                <div class="card-header" :id="'heading' + groupIndex">
                  <h5 class="mb-0">
                    <button class="btn btn-link" type="button" data-toggle="collapse" :data-target="'#collapse' + groupIndex" aria-expanded="true" :aria-controls="'collapse' + groupIndex">
                        {{ group.name }}
                    </button>
                  </h5>
                </div>
                <div :id="'collapse' + groupIndex" class="collapse" v-bind:class="{'show': groupIndex == 0}" :aria-labelledby="'heading' + groupIndex" data-parent="#groupAccordion">
                  <div class="card-body">
                   <div v-for="(mockup, mockupIndex) in getMockupsFromGroup(group.id)" :key="mockupIndex">
                        <Mockup :id="mockup.id" :name="mockup.name"/>
                    </div>
                  </div>
                </div>
            </div>
        </div>
        <div class="modal fade" id="newGroupModal" tabindex="-1" role="dialog" aria-labelledby="groupModalLabel" aria-hidden="true">
          <div class="modal-dialog" role="document">
            <div class="modal-content">
              <div class="modal-header">
                <h5 class="modal-title" id="groupModalLabel">Create a new group</h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div class="modal-body">
                  <input type="text" v-model="newGroupName" @keyup.enter="addGroup()" v-bind:class="{ 'is-invalid': groupNameIsInvalid}" class="form-control" placeholder="Add a new group name">
                  <div class="invalid-feedback">
                     Please choose a unique group name.
                  </div>
              </div>
              <div class="modal-footer">
                <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                <button type="button" class="btn btn-success" @click="addGroup()">Add</button>
              </div>
            </div>
          </div>
        </div>
        <div class="modal fade" id="newMockupModal" tabindex="-1" role="dialog" aria-labelledby="mockupModalLabel" aria-hidden="true">
          <div class="modal-dialog" role="document">
            <div class="modal-content">
              <div class="modal-header">
                <h5 class="modal-title" id="mockupModalLabel">Create a new mockup</h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div class="modal-body">
                  <div class="form-group">
                    <label for="mockupName">Name</label>
                    <input type="text" id="mockupName" v-model="newMockupName" @keyup.enter="addMockup()" v-bind:class="{ 'is-invalid': mockupNameIsInvalid}" class="form-control" placeholder="Add a new mockup name">
                    <div class="invalid-feedback">
                     Please choose a unique mockup name.
                    </div>
                  </div>
                  <div class="form-group">
                    <label for="mockupPosition">Position</label>
                    <input type="number" id="mockupPosition" v-model="position" class="form-control" placeholder="Add a mockup position">
                  </div>
                  <div class="form-group">
                    <label for="groupSelect">Choose the target group</label>
                    <select class="form-control" id="groupSelect">
                      <option v-for="(group, groupIndex) in groups" v-bind:key="groupIndex" v-bind:value="group.id">
                        {{ group.name }}
                      </option>
                    </select>
                  </div>
                  <div class="form-group">
                    <label for="groupSelect">Copy widgets from existing mockup</label>
                    <select class="form-control" id="mockupSelect">
                      <option value="0">none</option>
                      <option v-for="(mockup, mockupIndex) in mockups" v-bind:key="mockupIndex" v-bind:value="mockup.id">
                        {{ mockup.name }}
                      </option>
                    </select>
                  </div>
              </div>
              <div class="modal-footer">
                <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                <button type="button" class="btn btn-success" @click="addMockup()">Add</button>
              </div>
            </div>
          </div>
        </div>
    </div>
</template>

<script>
import uuid from 'uuid'
// TODO try to remove jquery
import $ from 'jquery'
import Mockup from '@/components/Mockup'
import {mapGetters} from 'vuex'

export default {
  name: 'MockupBar',
  data () {
    return {
      newGroupName: 'StartScene',
      newMockupName: 'EmptyMockup',
      position: 1,
      copyWidgets: false,
      mockupNameIsInvalid: false,
      groupNameIsInvalid: false
    }
  },
  methods: {
    addGroup () {
      this.groupNameIsInvalid = false
      if (this.newGroupName.length === 0) {
        this.groupNameIsInvalid = true
      } else {
        let store = this.$store
        let newGroupName = this.newGroupName
        let groupNameIsInvalid = this.groupNameIsInvalid
        store.getters.getGroups.forEach(function (group) {
          if (group.name === newGroupName && groupNameIsInvalid === false) {
            groupNameIsInvalid = true
          }
        })
        this.groupNameIsInvalid = groupNameIsInvalid
        let modal = $('#newGroupModal')

        if (this.groupNameIsInvalid === false) {
          let group = {
            'id': uuid.v4(),
            'name': this.newGroupName,
            'mockupIds': []
          }
          this.$store.dispatch('ADD_GROUP', group)
          this.newGroupName = 'StartScene'
          modal.find('.invalid-feedback').css('display', 'none')
          this.groupNameIsInvalid = false

          $('[data-dismiss=modal]').trigger({ type: 'click' })
        } else {
          modal.find('.invalid-feedback').css('display', 'block')
        }
      }
    },
    addMockup () {
      this.mockupNameIsInvalid = false
      if (this.newMockupName.length === 0) {
        this.mockupNameIsInvalid = true
      } else {
        let store = this.$store
        let newMockupName = this.newMockupName
        let mockupNameIsInvalid = this.mockupNameIsInvalid
        store.getters.getMockups.forEach(function (mockup) {
          if (mockup.name === newMockupName && mockupNameIsInvalid === false) {
            mockupNameIsInvalid = true
          }
        })
        this.mockupNameIsInvalid = mockupNameIsInvalid
        let modal = $('#newMockupModal')
        if (this.mockupNameIsInvalid === false) {
          let mockup = {
            'id': uuid.v4(),
            'position': this.position,
            'name': this.newMockupName,
            'active': true,
            'widgetIds': [],
            'groupId': $('#groupSelect').val()
          }
          let copyWidgetFromMockup = $('#mockupSelect').val()
          if (copyWidgetFromMockup !== '0') {
            let store = this.$store
            store.getters.getMockupsByIds(copyWidgetFromMockup)[0].widgetIds.forEach(function (widgetId) {
              let widget = store.getters.getWidgetsByIds(widgetId).pop()
              widget.mockupIds.push(mockup.id)
              mockup.widgetIds.push(widgetId)
            })
          }
          this.$store.dispatch('ADD_MOCKUP', mockup)
          this.newMockupName = 'EmptyMockup'
          this.position = 1
          modal.find('.invalid-feedback').css('display', 'none')
          this.mockupNameIsInvalid = false
          $('[data-dismiss=modal]').trigger({ type: 'click' })
        } else {
          modal.find('.invalid-feedback').css('display', 'block')
        }
      }
    }
  },
  components: {
    Mockup
  },
  computed: {
    ...mapGetters(
      [
        'getMockupsFromGroup'
      ]
    ),
    groups () {
      return this.$store.getters.getGroups
    },
    mockups () {
      return this.$store.getters.getMockups
    }
  }
}
</script>

<style>
    .btn {
        width: 100%;
        border-radius: 0rem;
    }
    .card-body {
        padding: 0;
    }
</style>



