<template>
    <div title="Button Widget" id="buttonWidget" class="widgetBar-element" @click="addNewWidget()">
        <svg id="button-widget-preview" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32" style="width: 24px;height: 24px;">
          <title>button_preview</title>
          <g>
            <rect x="0.25" y="8.25" width="31.5" height="15.5" style="fill: #fff"/>
            <path d="M31.5,8.5v15H.5V8.5h31M32,8H0V24H32V8Z"/>
          </g>
          <rect x="2" y="10" width="28" height="12" rx="1"/>
        </svg>
        <span>Button</span>
    </div>
</template>

<script>

import uuid from 'uuid'

export default {
  name: 'button-widget',
  data () {
    return {
      svgData: '<rect class="button-widget-border resizeable" fill="lightgrey" stroke="black" data-setting-style-fill="1" width="150" height="40"></rect>\n' +
                '<text x="75" y="20" font-size="20" class="button-widget-text" fill="black" data-setting-text-style-fill="1" data-setting-text-style-font-size="1" dominant-baseline="middle" text-anchor="middle" data-setting-value="1">Button</text>'
    }
  },
  methods: {
    addNewWidget () {
      let mockup = this.$store.getters.getCurrentMockup
      let widget = {
        'id': uuid.v4(),
        'type': 'Button',
        'active': true,
        'name': '',
        'svgData': this.svgData,
        'mockupIds': [
          mockup.id
        ],
        'x': 0,
        'y': 0,
        'settings': {
          'value': 'Button',
          'style-fill': 'lightgrey',
          'text-style-fill': 'black',
          'text-style-font-size': '20'
        }
      }
      this.$store.dispatch('ADD_WIDGET', widget)
    }
  }
}
</script>

<style>
    .button-widget-border {
        fill: #ccc;
    }
    .button-widget-text {
        font-size: 20px;
        font-family: ArialMT, Arial;
    }
</style>



