<template>
    <div title="Input Widget" id="inputWidget" class="widgetBar-element" @click="addNewWidget()">
        <svg id="input-widget-preview" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32" style="width: 24px;height: 24px;">
          <title>input_preview</title>
          <g>
            <rect x="0.25" y="8.25" width="31.5" height="15.5" style="fill: #fff"/>
            <path d="M31.5,8.5v15H.5V8.5h31M32,8H0V24H32V8Z"/>
          </g>
          <rect x="2" y="9" width="2" height="14"/>
        </svg>
        <span>Input</span>
    </div>
</template>

<script>

import uuid from 'uuid'

export default {
  name: 'input-widget',
  data () {
    return {
      svgData: '<rect class="input-widget-border resizeable" stroke="black" fill="white" data-setting-style-stroke="1" width="150" height="40"></rect>\n' +
                '<text x="10" y="20" font-size="20" class="input-widget-text" fill="black" data-setting-text-style-font-size="1" dominant-baseline="middle" text-anchor="left" data-setting-value="1">input</text>'
    }
  },
  methods: {
    addNewWidget () {
      let mockup = this.$store.getters.getCurrentMockup
      let widget = {
        'id': uuid.v4(),
        'type': 'Input',
        'active': true,
        'name': '',
        'svgData': this.svgData,
        'mockupIds': [
          mockup.id
        ],
        'x': 0,
        'y': 0,
        'settings': {
          'value': 'Input',
          'style-stroke': '#000',
          'text-style-font-size': '20'
        }
      }
      this.$store.dispatch('ADD_WIDGET', widget)
    }
  }
}
</script>

<style>
    .input-widget-border {
        fill: #fff;
    }

    .input-widget-text {
        font-size: 20px;
        font-family: ArialMT, Arial;
    }
</style>



