import App from './App'
import uuid from 'uuid'
import Vue from 'vue'
import Vuex from 'vuex'

// TODO can it removed ?
require('./assets/js/editor.js')

Vue.config.productionTip = false
Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    groups: [],
    mockups: [],
    widgets: [],
    currentMockup: null,
    currentWidget: null
  },
  actions: {
    ADD_CURRENT_WIDGET_TO_MOCKUP (state, mockupId) {
      state.commit('addCurrentWidgetToMockup', mockupId)
    },
    ADD_GROUP (state, group) {
      state.commit('addGroup', group)
    },
    ADD_MOCKUP (state, mockup) {
      state.commit('addMockup', mockup)
    },
    ADD_WIDGET (state, widget) {
      state.commit('addWidget', widget)
    },
    DELETE_GROUP (state) {
      state.commit('deleteGroup')
    },
    DELETE_MOCKUP (state) {
      state.commit('deleteMockup')
    },
    DELETE_WIDGET (state) {
      state.commit('deleteWidget')
    },
    GENERATE_PROJECT_JSON (state, fileName) {
      state.commit('generateProjectJSON', fileName)
    },
    importProjectFromJSON (state, event) {
      state.commit('importProjectFromJSON', event)
    },
    REMOVE_CURRENT_WIDGET_FROM_MOCKUP (state, mockupId) {
      state.commit('removeCurrentWidgetFromMockup', mockupId)
    },
    SET_CURRENT_MOCKUP (state, mockupId) {
      state.commit('setCurrentMockup', mockupId)
    },
    SET_CURRENT_WIDGET (state, widgetId) {
      state.commit('setCurrentWidget', widgetId)
    },
    UPDATE_GROUP (state, data) {
      state.commit('updateGroup', data)
    },
    UPDATE_MOCKUP (state, data) {
      state.commit('updateMockup', data)
    },
    UPDATE_WIDGET (state, data) {
      state.commit('updateWidget', data)
    },
    CLONE_WIDGET (state) {
      state.commit('cloneWidget')
    },
    // TODO make from 2 one
    UPDATE_WIDGET_SETTING (state, payload) {
      state.commit('updateWidgetSetting', payload)
    },
    UPDATE_WIDGET_SETTINGS (state, payload) {
      state.commit('updateWidgetSettings', payload)
    }
  },
  mutations: {
    addCurrentWidgetToMockup (state, mockupId) {
      let currentWidget = state.currentWidget
      state.mockups.forEach(function (mockup) {
        if (mockup.id === mockupId || mockupId === '-1') {
          if (mockup.widgetIds.indexOf(currentWidget.id) === -1) {
            mockup.widgetIds.push(currentWidget.id)
            currentWidget.mockupIds.push(mockupId)
          }
        }
      })
    },
    addGroup (state, group) {
      state.groups.push(group)
    },
    addMockup (state, mockup) {
      state.mockups.push(mockup)
      state.groups.forEach(function (group) {
        if (group.id === mockup.groupId) {
          group.mockupIds.push(mockup.id)
        }
      })
      if (state.currentMockup !== null) {
        state.currentMockup.active = false
      }
      state.currentMockup = mockup
    },
    addWidget (state, widget) {
      state.widgets.push(widget)
      state.currentMockup.widgetIds.push(widget.id)
      state.currentWidget = widget
    },
    generateProjectJSON (state, fileName) {
      if (fileName.length === 0) {
        fileName = 'scenario.json'
      } else if (fileName.endsWith('.json') === false) {
        fileName = fileName + '.json'
      }
      let text = JSON.stringify(state)
      let blob = new Blob([text], { type: 'application/json' })
      let anchor = document.createElement('a')

      anchor.download = fileName
      anchor.href = (window.URL).createObjectURL(blob)
      anchor.dataset.downloadurl = ['application/json', anchor.download, anchor.href].join(':')
      anchor.click()
    },
    importProjectFromJSON (state, event) {
      let reader = new FileReader()
      let importedFile = event.target.files[0]
      let store = this
      reader.onload = (function (f) {
        return function (e) {
          try {
            let dataArray = JSON.parse(e.target.result)
            state.groups = dataArray.groups
            state.mockups = dataArray.mockups
            state.widgets = dataArray.widgets
            store.dispatch('SET_CURRENT_MOCKUP', dataArray.currentMockup.id)
            store.dispatch('SET_CURRENT_WIDGET', dataArray.currentWidget.id)
          } catch (ex) {
            alert('import failed.' + ex)
          }
        }
      })(importedFile)
      if (importedFile) {
        reader.readAsText(importedFile)
        event.target.value = ''
      }
    },
    updateGroup (state, data) {
      let group = state.groups.filter(group => group.id === state.currentMockup.groupId).pop()
      Object.keys(data).forEach(function (dataKey) {
        group[dataKey] = data[dataKey]
      })
    },
    updateMockup (state, data) {
      Object.keys(data).forEach(function (dataKey) {
        state.currentMockup[dataKey] = data[dataKey]
      })
    },
    updateWidget (state, data) {
      Object.keys(data).forEach(function (dataKey) {
        state.currentWidget[dataKey] = data[dataKey]
      })
    },
    updateWidgetSetting (state, payload) {
      let widget = state.currentWidget
      widget.svgData = payload.svgData
      widget.settings[payload.setting] = payload.color
    },
    updateWidgetSettings (state, payload) {
      let widget = state.currentWidget
      widget.svgData = payload.svgData
      if (payload.settings !== undefined) {
        Object.keys(payload.settings).forEach(function (settingKey) {
          widget.settings[settingKey] = payload.settings[settingKey]
        })
      }
      if (payload.resetPosition) {
        widget.x = 0
        widget.y = 0
      }
    },
    setCurrentMockup (state, mockupId) {
      let mockup = state.mockups.filter(mockup => mockup.id === mockupId).pop()
      state.mockups.forEach(function (tempMockup) {
        tempMockup.active = false
      })
      mockup.active = true
      state.currentMockup = mockup
    },
    deleteGroup (state) {
      let groupId = state.currentMockup.groupId
      let group = this.getters.getGroupById(groupId)
      if (state.groups.length > 1) {
        let store = this
        group.mockupIds.forEach(function (mockupId) {
          store.dispatch('SET_CURRENT_MOCKUP', mockupId)
          store.dispatch('DELETE_MOCKUP')
        })
        this.dispatch('SET_CURRENT_MOCKUP', state.mockups[0].id)
        this.dispatch('SET_CURRENT_WIDGET', state.mockups[0].widgetIds[0])
      }
    },
    deleteWidget (state) {
      let currentWidget = state.currentWidget
      state.widgets.forEach(function (widget, index, object) {
        if (widget.id === currentWidget.id) {
          object.splice(index, 1)
        }
      })
      state.mockups.forEach(function (mockup, index, object) {
        mockup.widgetIds.forEach(function (tempWidgetId, index, object) {
          if (tempWidgetId === currentWidget.id) {
            object.splice(index, 1)
          }
        })
      })
    },
    deleteMockup (state) {
      let mockup = state.currentMockup

      mockup.widgetIds.forEach(function (widgetId, index, object, store) {
        this.dispatch('SET_CURRENT_WIDGET', widgetId)
        this.dispatch('REMOVE_CURRENT_WIDGET_FROM_MOCKUP', mockup.id)
        this.dispatch('SET_CURRENT_MOCKUP', state.mockups[0].id)
      }, this)

      let group = this.getters.getGroupById(mockup.groupId)
      group.mockupIds.forEach(function (mockupId, index, object) {
        if (mockupId === mockup.id) {
          object.splice(index, 1)
        }
      })
      state.mockups.forEach(function (tempMockup, index, object) {
        if (tempMockup.id === mockup.id) {
          object.splice(index, 1)
        }
      })

      this.dispatch('SET_CURRENT_MOCKUP', state.mockups[0].id)
      this.dispatch('SET_CURRENT_WIDGET', state.mockups[0].widgetIds[0])
    },
    cloneWidget (state) {
      let currentWidget = state.currentWidget
      let mockup = state.currentMockup
      let widget = Object.assign({}, currentWidget)
      widget.id = uuid.v4()
      widget.name = '(clone)' + widget.name
      widget.mockupIds = [mockup.id]
      this.dispatch('REMOVE_CURRENT_WIDGET_FROM_MOCKUP', mockup.id)
      this.dispatch('ADD_WIDGET', widget)
    },
    removeCurrentWidgetFromMockup (state, mockupId) {
      let currentWidget = state.currentWidget
      currentWidget.mockupIds.forEach(function (tempMockupId, index, object) {
        if (tempMockupId === mockupId) {
          object.splice(index, 1)
        }
      })
      let mockup = state.mockups.filter(mockup => mockup.id === mockupId).pop()
      mockup.widgetIds.forEach(function (widgetId, index, object) {
        if (widgetId === currentWidget.id) {
          object.splice(index, 1)
        }
      })
      state.widgets.forEach(function (tempWidget, index, object) {
        if (tempWidget.mockupIds.length === 1 && tempWidget.mockupIds[0] === mockupId) {
          object.splice(index, 1)
        } else {
          tempWidget.mockupIds.forEach(function (tempMockupId, index2, object2) {
            if (tempWidget.mockupIds === mockupId) {
              object2.splice(index2, 1)
            }
          })
        }
      })
    },
    setCurrentWidget (state, id) {
      let widget = state.widgets.filter(widgets => widgets.id === id).pop()
      state.currentWidget = widget
    }
  },

  getters: {
    getCurrentMockup: state => state.currentMockup,
    getCurrentWidget: state => state.currentWidget,
    getGroupById: state => groupId => state.groups.filter(group => group.id === groupId).pop(),
    getGroups: state => state.groups,
    getMockupsByIds: state => mockupIds => state.mockups.filter(mockup => mockupIds.indexOf(mockup.id) !== -1),
    getMockups: state => state.mockups.sort(function (a, b) {
      return a.position >= b.position ? 1 : 0
    }),
    getMockupsFromGroup: state => groupId => state.mockups.filter(mockup => mockup.groupId === groupId),
    getWidgetsByIds: state => widgetIds => state.widgets.filter(widget => widgetIds.indexOf(widget.id) !== -1),
    getCurrentWidgets: state => state.widgets.filter(widget => state.currentMockup.widgetIds.indexOf(widget.id) !== -1)
  }
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  components: { App },
  template: '<App/>',
  mounted: function () {
    this.$nextTick(function () {
      let mockup = {
        id: uuid.v4(),
        name: 'StartMockup',
        active: true,
        position: 1,
        widgetIds: [],
        groupId: null
      }
      let group = {
        id: uuid.v4(),
        name: 'StartGroup',
        mockupIds: []
      }
      mockup.groupId = group.id

      // init data
      this.$store.dispatch('ADD_GROUP', group)
      this.$store.dispatch('ADD_MOCKUP', mockup)
    })
  }
})



