<template>
    <div class="col-md-3 settingBar" style="background-color: rgba(0, 0, 0, .1);">
        <ul class="nav nav-pills" id="pills-tab" role="tablist">
            <li class="nav-item">
                <a class="nav-link active" id="mockup-tab" data-toggle="pill" href="#mockups" role="tab" aria-controls="mockups" aria-selected="true">
                    Mockups
                </a>
            </li>
            <li class="nav-item">
                <a class="nav-link" id="settings-tab" data-toggle="pill" href="#settings" role="tab" aria-controls="settings" aria-selected="false">
                    Settings
                </a>
            </li>
            <li class="nav-item">
                <a class="nav-link" id="export-tab" data-toggle="pill" href="#export" role="tab" aria-controls="export" aria-selected="true">
                   Im-/Export
                </a>
            </li>
        </ul>
        <div class="tab-content" id="tabContent">
          <div class="tab-pane fade show active" id="mockups" role="tabpanel" aria-labelledby="mockups-tab">
              <MockupBar/>
          </div>
          <div class="tab-pane fade" id="settings" role="tabpanel" aria-labelledby="settings-tab">
              <Settings/>
          </div>
          <div class="tab-pane fade" id="export" role="tabpanel" aria-labelledby="export-tab">
              <Exports/>
          </div>
        </div>
    </div>
</template>

<script>

import MockupBar from '@/components/MockupBar'
import Settings from '@/components/Settings'
import Exports from '@/components/Exports'

export default {
  name: 'SettingBar',
  components: {
    MockupBar,
    Settings,
    Exports
  }
}
</script>

<style>
    .settingBar {
        box-shadow: inset 2px 0 0 rgba(0, 0, 0, .3);
        padding-right: 0px;
        padding-left: 2px;
        background-color: rgba(0, 0, 0, .1);

    }
    .nav {
        border-bottom: 1px solid black;
    }
    .nav-pills .nav-link.active, .nav-pills .show>.nav-link {
        color: #000;
        background-color: rgba(0,0,0,.08);
        border-bottom: 3px solid black;
    }
    .nav-item:hover {
        background-color: rgba(0,0,0,.08);
    }
    .nav-pills .nav-link {
        border-radius: 0rem;
    }
</style>



