import interact from 'interactjs'

interact('.draggable')
  .draggable({
    // enable inertial throwing
    inertia: true,
    // keep the element within the area of it's parent
    restrict: {
      restriction: 'parent',
      endOnly: true,
      elementRect: { top: 0, left: 0, bottom: 1, right: 1 }
    },
    // enable autoScroll
    autoScroll: true,
    // call this function on every dragmove event
    onmove: dragInDropZone
  })

interact('.resizeable').resizable({
  edges: { left: true, right: true, bottom: true, top: true },
  restrictEdges: {
    outer: 'parent',
    endOnly: true
  },
  restrictSize: {
    min: { width: 64, height: 32 }
  },
  max: Infinity

}).on('resizemove', function (event) {
  var target = event.target
  var x = (parseFloat(target.parentNode.getAttribute('data-x')) || 0)
  var y = (parseFloat(target.parentNode.getAttribute('data-y')) || 0)

  // update the element's style
  target.setAttribute('width', event.rect.width + 'px')
  target.setAttribute('height', event.rect.height + 'px')

  // translate when resizing from top or left edges
  x += event.deltaRect.left
  y += event.deltaRect.top

  target.parentNode.style.webkitTransform = target.parentNode.style.transform = 'translate(' + x + 'px,' + y + 'px)'

  target.parentNode.setAttribute('data-x', x)
  target.parentNode.setAttribute('data-y', y)
})

function dragInDropZone (event) {
  var target = event.target
  // keep the dragged position in the data-x/data-y attributes
  var x = (parseFloat(target.getAttribute('data-x')) || 0) + event.dx
  var y = (parseFloat(target.getAttribute('data-y')) || 0) + event.dy

  // translate the element
  target.style.webkitTransform = 'translate(' + x + 'px, ' + y + 'px)'
  target.style.transform = 'translate(' + x + 'px, ' + y + 'px)'

  // update the posiion attributes
  target.setAttribute('data-x', x)
  target.setAttribute('data-y', y)
}

interact('.dropzone').dropzone({
  overlap: 0.50,
  ondropactivate: function (event) {
    event.target.classList.add('drop-active')
  },
  ondragenter: function (event) {
    let dropzoneElement = event.target
    dropzoneElement.classList.add('drop-target')
  },
  ondragleave: function (event) {
    event.target.classList.remove('drop-target')
  },
  ondrop: function (event) {},
  ondropdeactivate: function (event) {
    event.target.classList.remove('drop-active')
    event.target.classList.remove('drop-target')
  }
})



