<template>
    <div title="Div Widget" id="divWidget" class="widgetBar-element" @click="addNewWidget()">
        <svg id="div-widget-preview" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32" style="width: 24px;height: 24px;">
          <title>Div Preview</title>
          <g>
            <rect x="0.25" y="0.25" width="31.5" height="31.5" style="fill: #fff"/>
            <path d="M31.5.5v31H.5V.5h31M32,0H0V32H32V0Z"/>
          </g>
        </svg>
        <span>DIV</span>
    </div>
</template>

<script>

import uuid from 'uuid'

export default {
  name: 'div-widget',
  data () {
    return {
      svgData: '<rect class="resizeable" width="100px" height="100px" stroke="#000000" data-setting-style-stroke="1" fill-opacity="0"></rect>'
    }
  },
  methods: {
    addNewWidget () {
      let mockup = this.$store.getters.getCurrentMockup
      let widget = {
        'id': uuid.v4(),
        'type': 'DIV',
        'active': true,
        'name': '',
        'svgData': this.svgData,
        'mockupIds': [
          mockup.id
        ],
        'settings': {
          'style-stroke': '#000'
        },
        'x': 0,
        'y': 0
      }
      this.$store.dispatch('ADD_WIDGET', widget)
    }
  }
}
</script>



