<template>
    <main class="col drawing-area dropzone">
        <svg version="1.1" viewBox="0 0 800 800" width="100%" height="100%" id="dropzone" xmlns="http://www.w3.org/2000/svg"  xmlns:xlink="http://www.w3.org/1999/xlink">
            <g v-for="(widget, index) in widgets" v-if="widget.active" :key="index" class="draggable" v-html="widget.svgData" v-bind:id="widget.id" v-bind:data-x="widget.x"  v-bind:data-y="widget.y" :style="transform(widget.x,widget.y)" @click="updateWidgetPosition(widget.id)" v-on:dblclick="openWidgetSettingsModal(widget.id)">
            </g>
        </svg>
    </main>
</template>

<script>
export default {
  name: 'Content',
  methods: {
    transform (x, y) {
      return `transform: translate(${x}px, ${y}px)`
    },
    openWidgetSettingsModal (widgetId) {
      document.getElementById('settings-tab').click()
      document.getElementById('settings-widget-' + widgetId).click()
    },
    updateWidgetPosition (widgetId) {
      this.$store.dispatch('SET_CURRENT_WIDGET', widgetId)
      let widgetElement = document.getElementById(widgetId)
      let currentWidget = this.$store.getters.getCurrentWidget
      if (currentWidget.type === 'Button') {
        widgetElement.children[1].setAttribute('x', widgetElement.children[0].width.baseVal.value / 2)
        widgetElement.children[1].setAttribute('y', widgetElement.children[0].height.baseVal.value / 2)
      } else if (currentWidget.type === 'Input') {
        widgetElement.children[1].setAttribute('y', widgetElement.children[0].height.baseVal.value / 2)
      }

      let data = {
        'id': widgetId,
        'x': widgetElement.dataset.x,
        'y': widgetElement.dataset.y,
        'svgData': widgetElement.innerHTML
      }
      this.$store.dispatch('UPDATE_WIDGET', data)
    }
  },
  computed: {
    widgets () {
      let currentMockup = this.$store.getters.getCurrentMockup
      let widgets = []
      if (currentMockup !== null) {
        widgets = this.$store.getters.getWidgetsByIds(currentMockup.widgetIds)
        widgets.sort(function (a, b) {
          return (b.type === 'DIV') - (a.type === 'DIV')
        })
      }
      return widgets
    }
  }
}
</script>

<style scoped>
    .dropzone {
        padding: 10px;
        border: dashed 4px transparent;
        float: left;
        height: 100%;
        width: 100%;
    }
    .dropzone svg {
        width: 100%;
    }
    .drop-active {
        border-color: #aaa;
    }
</style>



