<template>
    <div title="Text Widget" id="textWidget" class="widgetBar-element" @click="addNewWidget()">
        <svg id="text-widget-preview" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32" style="width: 24px;height: 24px;">
          <title>text_preview</title>
          <g>
            <rect x="0.25" y="0.25" width="31.5" height="31.5" style="fill: #fff"/>
            <path d="M31.5.5v31H.5V.5h31M32,0H0V32H32V0Z"/>
          </g>
          <text transform="translate(7.99 23.15)" style="font-size: 24px;font-family: Arial;letter-spacing: -0.05517578125em">A</text>
          <line x1="7.95" y1="26" x2="26" y2="26" style="fill: none;stroke: #000;stroke-miterlimit: 10;stroke-width: 0.5px"/>
        </svg>
        <span>Text</span>
    </div>
</template>

<script>

import uuid from 'uuid'

export default {
  name: 'text-widget',
  data () {
    return {
      svgData: '<text height="100%" font-size="20" class="text" transform="translate(0 23.22) scale(0.77 1)" data-setting-style-font-size="1" data-setting-style-fill="1" data-setting-value="1">Text</text>'
    }
  },
  methods: {
    addNewWidget () {
      let mockup = this.$store.getters.getCurrentMockup
      let widget = {
        'id': uuid.v4(),
        'type': 'Text',
        'active': true,
        'name': '',
        'svgData': this.svgData,
        'mockupIds': [
          mockup.id
        ],
        'x': 0,
        'y': 0,
        'settings': {
          'value': 'Text',
          'style-fill': '#000',
          'style-font-size': '20'
        }
      }
      this.$store.dispatch('ADD_WIDGET', widget)
    }
  }
}
</script>

<style>
    .text-widget {
        font-size: 27.05px;
        font-family: ArialMT, Arial;
    }
</style>



